import { Injectable } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CommonService {
  public pageLoaderData = new BehaviorSubject<any>('');
  currentPageLoaderData = this.pageLoaderData.asObservable();

  public CiHeaderData = new BehaviorSubject<any>('');
  CiHeaderDatainfo = this.CiHeaderData.asObservable();

  public CiUserHeaderData = new BehaviorSubject<any>('');
  CiuserHeaderDatainfo = this.CiUserHeaderData.asObservable();

  public menuShow = new BehaviorSubject<boolean>(false);
  sidemenuHeader = this.menuShow.asObservable();

  public siHeaderData = new BehaviorSubject<any>('');
  SIHeaderDatainfo = this.siHeaderData.asObservable();

  public ciChildrData = new BehaviorSubject<any>('');
  CIchildDataList = this.ciChildrData.asObservable();

  public suggestedId = new BehaviorSubject<any>('');
  suggestedCIList = this.suggestedId.asObservable();

  public ciRelationshipTable = new BehaviorSubject<any>('');
  relationshipTable = this.ciRelationshipTable.asObservable();

  public ciRelationshipDiagram = new BehaviorSubject<any>('');
  relationshipDiagram = this.ciRelationshipDiagram.asObservable();

  public CiSuggestedNew = new BehaviorSubject<any>('');
  SuggestedListNew = this.CiSuggestedNew.asObservable();

  public ciDynamicData = new BehaviorSubject<any>('');
  ciDynamicFieldList = this.ciDynamicData.asObservable();

  


  /* Service type Implementation */
  public siChildData = new BehaviorSubject<any>('');
  SIchildDataList = this.siChildData.asObservable();

  public siDynamicData = new BehaviorSubject<any>('');
  siDynamicFieldList = this.siDynamicData.asObservable();

  public SisuggestedlistId = new BehaviorSubject<any>('');
  suggestedlistIdservicelist = this.SisuggestedlistId.asObservable();

  public servicemapId = new BehaviorSubject<any>('');
  servicemapitemId = this.servicemapId.asObservable();

  public mapSiListEdit = new BehaviorSubject<any>('');
  mapsiListnew = this.mapSiListEdit.asObservable();

  public mapSourceSI = new BehaviorSubject<any>('');
  sourceSIName = this.mapSourceSI.asObservable();

  constructor(private router: Router) {
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.checkPage(event);
      }
    });
  }

  updateCiDetailInfo(data) :any{
    this.CiHeaderData.next(data)
  }
  
  updateuserCiDetailInfo(data) :any{
    this.CiUserHeaderData.next(data)
  }
  updateSIDetailInfo(data) :any{
    this.siHeaderData.next(data)
  }
  updateLoaderStatus(data): any {
    this.pageLoaderData.next(data);
  }

  updateHeaderShow(data): any {
    this.menuShow.next(data);
  }

  checkPage(event) {
   
    const urls = (event.url + '').split('/');
    // console.log("url ",urls);
    if(urls.length>2){
      this.menuShow.next(true); 
    }else{
      this.menuShow.next(false); 
    }
    
    /*switch (urls) {
      case 'login': {
        this.menuShow.next(false);
        break;
      }
     
      default: {
        this.menuShow.next(true);
        break;
      }
    }*/

  }
  updateCIchildData(data): any {
    this.ciChildrData.next(data);
  }
  
  updateDynamicFieldinfo(data): any {
    this.ciDynamicData.next(data);
  }
  // update
  updateCIid(data): any {
    // console.log(data);
    this.suggestedId.next(data);
  }

  updateRelationshipTable(data): any {
    this.ciRelationshipTable.next(data);
  }
  updateRelationshipDiagram(data): any {
    this.ciRelationshipDiagram.next(data);
  }
  newSuggestedList(data): any {
    this.CiSuggestedNew.next(data);
  }

  updateSIchildData(data): any {
    this.siChildData.next(data);
  }
  updateSIDynamicFieldinfo(data): any {
    // console.log(data);
    this.siDynamicData.next(data);
  }

  updateSISuggesttedData(data): any{
    this.SisuggestedlistId.next(data);
  }
  
  updateMapServiceID(data): any{
    this.servicemapId.next(data);
  }

  updateMapServicelist(data): any{
    this.mapSiListEdit.next(data);
  }

  updateSourceSiName(data): any{
    this.mapSourceSI.next(data);
  }
}
