import { Component, OnInit } from '@angular/core';
import { MenuItem } from 'primeng/api';
import { CommonService } from '../../../cmdb-common/services/common.service';
import { TokenStorageService } from '../../../base/services/auth/token-storage.service';
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  headerMenu: MenuItem[];
  checkLogin: any;
  constructor( private tokenStorage: TokenStorageService,private commonSevice: CommonService) { }

  ngOnInit(): void {
    this.getUserDetails();
    this.headerMenu = [
      {
        styleClass: "questionmark"
         },
      {
         
        styleClass: "notification"
       
      },
      {
          label: this.checkLogin,
          styleClass: "different"
      },
      {
        icon: 'pi pi-fw1 pi-sign-out', command: (click) => {
             this.commonSevice.updateHeaderShow(false);
             this.tokenStorage.signOut();
             window.location.reload();  
          },
          styleClass: "logout-alignment",
          title: 'Log out'
      }
  ];
  
  }

  getUserDetails(){
    // this.tokenStorage.getUser().subscribe(data => {
    //     console.log(data);
    //   });
    var user = this.tokenStorage.getUser();
    // console.log(user);
    this.checkLogin = user.username;
    // console.log(this.checkLogin);
    

  }

}
