import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './base/components/login/login.component';

import { AuthGuardService as AuthGuard
} from './base/services/auth/auth-guard.service';
import { PageNotFoundComponent } from './cmdb-common/components/page-not-found/page-not-found.component';


const routes: Routes = [

  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'configuration', canActivate: [AuthGuard], loadChildren: () => import('./module/configuration/configuration.module').then(m => m.ConfigurationModule) },
  { path: 'reports', loadChildren: () => import('./module/report/report.module').then(m => m.ReportModule) },
  { path: 'page-not-found', component: PageNotFoundComponent },
  { path: 'users', canActivate: [AuthGuard], loadChildren: () => import('./module/users/users.module').then(m => m.UsersModule) },


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
