import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FormserviceService {

  public formEnableData = new BehaviorSubject<any>('');
  isFormEnable = this.formEnableData.asObservable();

  constructor() { }

  updateFormEnable(data) :any{
    console.log(data);
    this.formEnableData.next(data)
  }
}
