import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { newUrl} from '../../../environments/environment';
import { configUrl } from '../../../environments/serviceurl';

@Injectable({
  providedIn: 'root'
})
export class LeftmenuService {

  constructor(private http: HttpClient) { }

  getLeftMenuList(): Observable<any> {
    return this.http.get(`${newUrl}${configUrl.leftMenuListAPI}`);
  }
}
